<template>
  <a
    :href="href"
    :target="target"
    class="button"
    :style="styles">
    {{text}}
  </a>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      default: "",
    },
    href: {
      type: String,
      default: "",
    },
    target: {
      type: String,
      default: "",
    },
    backgroundColor: {
      type: String,
      default: "#e76d89",
    },
    textColor: {
      type: String,
      default: "white",
    },
  },
  computed: {
    styles: function() {
      return {
        backgroundColor: this.backgroundColor,
        color: this.textColor,
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.button {
  margin: 0 auto;
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  border-radius: 8px;
}
</style>



