<template>
  <Width100 classnames="about_me_container" :addPadding="true">
    <Width50>
      <Width100 :addPadding="true">
        <div class="me_img">
          <VueImage :src="mai" />
        </div>
      </Width100>
      <Width100>
        <div class="view_resume_container">
          <AnchorButton
            href="/static/Mai_Nguyen.pdf"
            target="_blank"
            text="View Resume"
          />
        </div>
      </Width100>
    </Width50>
    <Width50>
      <div class="me-paragraph">
        <Paragraph v-for="(info, idx) in aboutMe"
          v-bind:key="idx"
          :paragraph="info"
        />
      </div>
    </Width50>
  </Width100>
</template>

<script>
import AnchorButton from '../common/AnchorButton';
import VueImage from '../common/VueImage';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import mai from "../../assets/img/mai.png";
import ign_header from "../../assets/img/ign_header.png";

export default {
    name: 'AboutMe',
    components: {
      AnchorButton,
      Paragraph,
      Width50,
      Width100,
      VueImage,
    },
    mounted () {
      this.$ga.page({
        page: '/about_me',
        title: 'About Me',
        location: window.location.href
      });
    },
    data() {
      return {
        mai,
        aboutMe: [
          "Greetings! My name is Mai Nguyen. My regular days consists of mapping users' experience thanks to the methods of Design Thinking. I enjoy it a lot, even outside the digital world. I like to analyze interactivity in everyday life and explore concepts of aesthetics and interactive design. My goal is to make technicalities of technology accessible and easy to understand for people.",
          "Before, I graduated from San Jose State University with a Bachelor's Degree in Business. Many people wonder \"What made you change your study?\" Working 2 years at Apigee Corp. & Peloton Technology sparked up my interest into tech. I gathered the courage and started a Bootcamp at CareerFoundry and realized generating creative ideas from user's point of view to create life-impactful design and beautiful experiences is what I enjoy doing.",
          "My interests include: Playing jazz music on the piano, reading scifi books, kickboxing, creating youtube videos, learning programming languages on the side, and eating avocado toast.",
        ],
      }
    }
};
</script>

<style lang="scss" scoped>
.about_me_container {
  height: 100vh;
  margin-top: 32px;

  .paragraph-container {
    margin-bottom: 16px;
  }
}

.view_resume_container {
  text-align: center;
  margin: 32px auto 0;
}

.me_img {
  margin: 0 auto;
  width: 300px;
  height: 300px;
}
.me-paragraph {
  line-height: 2;
}

@media screen and (max-width: 900px) {
  .about_me_container {
    height: 100%;
    padding: 32px;
  }

  .me_img {
    margin: 0 auto;
    width: 80%;
    height: 80%;
  }
  .view_resume_container {
    margin-bottom: 16px;
  }
}

</style>



